<template>
  <div>
    <transition-group name="fade" tag="div" :style="dimension" class="relative center mw-100">
      <div v-for="i in [currentIndex]" :key="i" class="center">
        <router-link :to="currentUrl" >
          <img class="mw-100" :src="currentImg" :style="dimension" />
        </router-link>
      </div>
    </transition-group>

    <div class="center">
      <a class="prev" @click="prev" href="javascript:void(0)">
        <font-awesome-icon
          :icon="['fas', 'chevron-circle-left']"
          size="lg"
          :style="{ color: 'LightSlateGrey' }"
        />
      </a>
      <!--
      <input
        v-for="(item, index) in images"
        :key="index"
        type="radio"
        :id="'slide' + index"
        @click="selected(index)"
        :checked="currentIndex===index"
      />
      -->
      <span
        v-for="(item, index) in images"
        :key="index"
        :id="'slide' + index"
        @click="selected(index)"
        :class="dotClass(currentIndex===index)"
      ></span>
      <a class="next" @click="next" href="javascript:void(0)">
        <font-awesome-icon
          :icon="['fas', 'chevron-circle-right']"
          size="lg"
          :style="{ color: 'LightSlateGrey' }"
        />
      </a>
    </div>
  </div>
</template>

<script>
// https://github.com/alligatorio/vue-image-slider
// https://www.digitalocean.com/community/tutorials/vuejs-create-image-slider

import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faChevronCircleLeft,
  faChevronCircleRight,
} from "@fortawesome/free-solid-svg-icons";

library.add(faChevronCircleLeft, faChevronCircleRight);

export default {
  name: "SliderThird",
  props: {
    dimension: {
      type: String,
      default: "height:300px;",
    },
    images: {
      type: Array,
      default: [
        {
          src:
            "https://cdn.pixabay.com/photo/2015/12/12/15/24/amsterdam-1089646_1280.jpg",
          url: "#1",
        },
        {
          src:
            "https://cdn.pixabay.com/photo/2016/02/17/23/03/usa-1206240_1280.jpg",
          url: "#2",
        },
        {
          src:
            "https://cdn.pixabay.com/photo/2015/05/15/14/27/eiffel-tower-768501_1280.jpg",
          url: "#3",
        },
        {
          src:
            "https://cdn.pixabay.com/photo/2016/12/04/19/30/berlin-cathedral-1882397_1280.jpg",
          url: "#4",
        },
      ],
    },
  },
  data() {
    return {
      timer: null,
      currentIndex: 0,
      //dimension: "height:300px;max-width:100%",
    };
  },
  mounted: function () {
    this.startSlide();
  },
  methods: {
    startSlide: function () {
      this.timer = setInterval(this.next, 4000);
    },
    next: function () {
      // this.currentIndex += 1
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
      this.resetTimer();
    },
    prev: function () {
      // this.currentIndex -= 1
      this.currentIndex =
        (this.currentIndex + this.images.length - 1) % this.images.length;
      this.resetTimer();
    },
    selected: function (index) {
      this.currentIndex = index;
      this.resetTimer();
    },
    resetTimer: function () {
      clearInterval(this.timer);
      this.timer = setInterval(this.next, 4000);
    },
    dotClass(active) {
      return {
        dot: !active,
        "dot-active": active,
        "mx-1": true,
      };
    },
  },
  computed: {
    currentImg: function () {
      // return this.images[Math.abs(this.currentIndex) % this.images.length];
      return this.images[Math.abs(this.currentIndex) % this.images.length].src;
    },
    currentUrl: function () {
      return this.images[Math.abs(this.currentIndex) % this.images.length].url;
    },
  },
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.9s ease;
  /*transition: opacity .3s;*/
  overflow: hidden;
  visibility: visible;
  position: absolute;
  width: 100%;
  opacity: 1;
}

.fade-enter,
.fade-leave-to {
  visibility: hidden;
  width: 100%;
  opacity: 0;
}
/*
img {
  height:600px;
  width:100%
}
*/

/* https://www.w3schools.com/howto/howto_css_circles.asp */
.dot {
  height: 12px;
  width: 12px;
  background-color: LightGray;
  border-radius: 50%;
  display: inline-block;
}

.dot:hover {
  background-color: Gray;
}

.dot-active {
  height: 12px;
  width: 12px;
  background-color: Gray;
  border-radius: 50%;
  display: inline-block;
}

.prev,
.next {
  cursor: pointer;
  /*
  position: absolute;
  top: 40%;
  */
  width: auto;
  /*padding: 16px;*/
  padding: 5px;
  /*color: white;*/
  color: blue;
  font-weight: bold;
  font-size: 18px;
  transition: 0.7s ease;
  /*border-radius: 0 4px 4px 0;*/
  /*border-radius: 50%;*/
  border-radius: 4px 4px 4px 4px;
  text-decoration: none !important;
  user-select: none;
}

/* Position the "next button" to the right */
/*
.next {
  right: 0;
}

.prev {
  left: 0;
}
*/
/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  /*background-color: rgba(0,0,0,0.9);*/
  /*background-color: rgba(177, 185, 194, 0.9);*/
  background-color: rgba(223, 230, 235, 0.9);
}

.center {
  /*
  margin: auto;
  width: 100%;
  padding: 10px;
  */
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

/* fix image resizing in transition */
.relative {
  position: relative;
}

input[type="radio" i] {
  background-color: initial;
  cursor: default;
  appearance: radio;
  box-sizing: border-box;
  margin: 3px 3px 0px 5px;
  padding: initial;
  border: initial;
}
</style>
