<template>
  <MyTemplate>
    <template v-slot:myContent>
      <b-row>
        <b-col lg="3" class="mt-3">
          <div class="list-group">
            <li class="list-group-item list-group-item-primary">書目分類</li>
            <TreeMenu :listData="list"></TreeMenu>
          </div>
        </b-col>

        <b-col lg="9">
          <b-row class="mt-3">
            <b-col lg="3" md="12" class="mt-3 justify-content-center">
              <a :href="book.url">
                <img :src="book.img" alt class="img-fluid" />
              </a>
            </b-col>

            <b-col lg="6" md="12" class="mt-3">
              <ul class="list-unstyled">
                <div itemscope itemtype="http://schema.org/Book">
                  <li class="bg-light text-dark">
                    <h5>
                      <span class itemprop="name" style>{{ book.title }}</span>
                    </h5>
                  </li>
                  <li>
                    <span class="info-title">作者&nbsp;&colon;&nbsp;</span>
                    <span class="info-content" itemprop="author">{{
                      book.author
                    }}</span>
                  </li>
                  <li v-if="hasValue">
                    <span class="info-title">譯者&nbsp;&colon;&nbsp;</span>
                    <span class="info-content" itemprop="author">{{
                      book.translator
                    }}</span>
                  </li>
                  <li>
                    <span class="info-title">出版社&nbsp;&colon;&nbsp;</span>
                    <span class="info-content" itemprop="publisher">{{
                      book.pulbisherName
                    }}</span>
                  </li>
                  <li>
                    <span class="info-title">出版日期&nbsp;&colon;&nbsp;</span>
                    <span class="info-content" itemprop="datePublished">{{
                      book.publicationDate
                    }}</span>
                  </li>
                  <li>
                    <span class="info-title">頁數&nbsp;&colon;&nbsp;</span>
                    <span class="info-content" itemprop="numberOfPages">{{
                      book.pages
                    }}</span>
                  </li>
                  <li>
                    <span class="info-title">ISBN&nbsp;&colon;&nbsp;</span>
                    <span class="info-content" itemprop="isbn">{{
                      book.isbn
                    }}</span>
                  </li>
                </div>
                <li>
                  <span class="info-title">定價&nbsp;&colon;&nbsp;</span>
                  <span class="info-content">{{ book.listPrice }}元</span>
                </li>
                <div itemscope itemtype="http://schema.org/Offer">
                  <li>
                    <span class="info-title">優惠價&nbsp;&colon;&nbsp;</span>
                    <span class="info-content">
                      <span class="text-danger">{{ book.discount }}</span
                      >折&nbsp;
                      <span
                        class="text-danger"
                        itemprop="price"
                        v-text="salePrice(book)"
                      ></span
                      >元
                    </span>
                  </li>
                </div>
              </ul>
            </b-col>

            <b-col lg="3" md="12" class="mt-3">
              <div class="text-left">
                <span
                  v-text="showInventory(book.inventory)"
                  :class="showDanger(book.inventory)"
                ></span>
              </div>
              <div class="mt-1">
                <b-button
                  block
                  variant="primary"
                  class="text-left"
                  :disabled="!book.inventory"
                >
                  <font-awesome-icon
                    :icon="['fas', 'shopping-cart']"
                  />&nbsp;放入購物車
                </b-button>
              </div>
              <div class="mt-1">
                <b-button
                  block
                  variant="success"
                  class="text-left"
                  :disabled="!book.inventory"
                >
                  <font-awesome-icon
                    :icon="['fas', 'money-check-alt']"
                  />&nbsp;結帳
                </b-button>
              </div>
              <div class="mt-1">
                <b-button block variant="warning" class="text-left">
                  <font-awesome-icon :icon="['far', 'heart']" />&nbsp;加入追蹤
                </b-button>
              </div>
            </b-col>
          </b-row>

          <!-- new row -->
          <b-col cols="12" class="mt-3">
            <b-alert show variant="primary" class="mt-5">商品描述</b-alert>
            <div class="item-desc">
              <p>
                進行測試驅動開發、領域驅動設計及製作事件驅動微服務
                <br />
                <br />「這是一本社群期待已久的書籍，讓大家看到現代app架構的核心在Python裡面究竟長怎樣！Harry與Bob展示了依賴反轉原則在這種時尚、動態的語言中有多麼優雅。」
                <br />—Brandon Rhodes <br />Python-patterns.guide作者
                <br />
                <br />「截至目前為止，坊間幾乎沒有介紹使用Python編寫可維護大型系統的Python原生教材。本書展示Python可以遊刃有餘地進行重要的軟體開發。」
                <br />—Hynek Schlawack <br />Python忠實支持者、部落客及演說者
                <br />
                <br />隨著Python越來越流行，專案的規模也越來越大、越來越複雜，讓許多Python開發者對高階軟體設計模式產生濃厚的興趣，例如六邊形/整潔架構、事件驅動架構，和以領域驅動設計(DDD)制定的策略模式，但是將這些模式轉換成Python不是那麼容易。
                <br />
                <br />透過這本實戰指南，來自MADE.com的Harry Percival與Bob
                Gregory介紹一些行之有效的架構設計模式，協助Python開發者管理app的複雜性，並且充分利用測試套件。
                <br />
                <br />作者透過具體的範例和優美、典型的Python來介紹各種模型，避免Java與C#語法的冗長表述。
                <br />
                <br />本書介紹的模式包括：
                <br />‧依賴反轉，以及它和port及adapter(六邊形/整潔架構)的關係
                <br />‧領域驅動設計與Entity、Value Object及Aggregate的區別
                <br />‧持久保存機制的Repository和Unit of Work模式
                <br />‧事件、指令和訊息匯流排 <br />‧命令查詢責任隔離(CQRS)模式
                <br />‧事件驅動架構與反應式微服務
                <br />
                <br />Harry Percival
                在2009年就開始使用Python了，他也是TDD和XP的粉絲。他是O’Reilly書籍《測試驅動開發｜使用Python》的作者，此書的副標題更是聞名—服從測試羊。
                <br />
                <br />Bob Gregory
                使用領域驅動設計來建構事件驅動系統已有十多年之久了，他曾經用過的語言包括C#、F#、Python與TypeScript。
              </p>
            </div>

            <b-alert show variant="primary" class="mt-5">目錄大綱</b-alert>
            <div class="item-desc">
              <p>
                前言
                <br />引言
                <br />
                <br />【第一部分 建立架構來支援領域模型的建構】 <br />第一章
                建立領域模型 <br />第二章 Repository 模式 <br />第三章
                簡短插曲：關於耦合與抽象 <br />第四章 我們的第一個用例：Flask
                API 與服務層 <br />第五章 高速檔與低速檔的 TDD <br />第六章 Unit
                of Work 模式 <br />第七章 Aggregate 與一致性界限
                <br />
                <br />
                <br />【第二部分 事件驅動架構】 <br />第八章 事件與 Message Bus
                <br />第九章 搭著 Message Bus 進城 <br />第十章 指令與指令處理式
                <br />第十一章 事件驅動架構：使用事件來整合微服務 <br />第十二章
                指令查詢責任隔離（CQRS） <br />第十三章 依賴注入（與啟動）
                <br />
                <br />結語 <br />附錄 A 總結圖表 <br />附錄 B 模板專案結構
                <br />附錄 C 換掉基礎設施：用 CSV 做每一件事 <br />附錄 D
                Repository 與 Unit of Work 模式，使用 Django <br />附錄 E 驗證
                <br />
                <br />索引
              </p>
            </div>
          </b-col>

          <b-col md="12" class="mt-5">
            <SliderThird :images="banner" :dimension="bannerDimension" />
          </b-col>
        </b-col>
      </b-row>
    </template>
  </MyTemplate>
</template>
 
<script>
import shop from "@/api/shop.js";
import config from "@/common/config";
import MyTemplate from "@/components/MyTemplate.vue";
import TreeMenu from "@/components/TreeMenu.vue";
import { library } from "@fortawesome/fontawesome-svg-core";
import SliderThird from "@/components/SliderThird.vue";
//import { faShoppingCart, faMoneyCheckAlt, faHeart } from '@fortawesome/free-solid-svg-icons'
import {
  faShoppingCart,
  faMoneyCheckAlt,
} from "@fortawesome/free-solid-svg-icons";
import { faHeart } from "@fortawesome/free-regular-svg-icons";

library.add(faShoppingCart, faMoneyCheckAlt, faHeart);

export default {
  name: "Product",
  data() {
    return {
      list: config.treeMenu,
      banner: config.banner,
      bannerDimension: "height:180px;",
      book: null,
    };
  },
  components: {
    MyTemplate,
    TreeMenu,
    SliderThird,
  },
  mounted() {},
  methods: {
    fetchData() {
      console.log("fetchData() isbn: " + this.$route.params.isbn);
      this.book = shop.getProduct(this.$route.params.isbn);
      console.log(this.book);
      if (!this.book) {
        alert("無此資料");
        this.$router.push("/404");
      }
      //this.$store.dispatch('products/getBrandProducts', {
      //  key: this.$route.params.brand
      //});
    },
    salePrice(obj) {
      return this.calSalePrice(obj.listPrice, obj.discount);
    },
    calSalePrice(price, discount) {
      return Math.round((price * discount) / 100);
    },
    showInventory(inventory) {
      const INVENTORY = "庫存";
      const OUT_OF_STOCK = "已無庫存";
      if (inventory == 0) {
        return OUT_OF_STOCK;
      } else if (inventory <= 10) {
        return INVENTORY + " = " + inventory;
      } else {
        return INVENTORY + " > 10";
      }
    },
    showDanger(inventory) {
      return {
        "text-danger": inventory == 0 ? true : false,
      };
    },
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    $route: "fetchData",
  },
  created() {
    console.log("created() isbn: " + this.$route.params.isbn);
    this.fetchData();
  },
  computed: {
    hasValue() {
      return this.book && this.book.translator.length != 0;
    },
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.info-title {
  /*
  width: 50px;
  display: inline-block !important;
  */
  display: block;
  width: 100px;
  color: #3d3f46;
  position: absolute;
}
.info-content {
  padding-left: 100px;
  display: block;
}
</style>